.search-results {
  max-width: 1000px;
  margin: auto;
}

.business-result {
  margin: 10px 0px 10px;
  padding: 5px 0px 5px;
  border-bottom: 1px dotted #ccc;
  transition: ease 500ms;
  background: url(/assets/images/logo-symbol-only-transparent.png) no-repeat;
  background-size: auto 100px;
  background-position: -140px center;
}

.business-result .business-pic {
  vertical-align: middle;
  height: 100px;
  width: 100px;
  line-height: 95px;
  padding: 2px;
}

.business-result .business-pic img {
  vertical-align: middle;
  min-width: 100%;
}

.business-result.active {
  background-position: center right;
}

#search {
  min-height: 900px;
}

#search .row {
  margin: 0px;
}

.side-map {
  margin-top: 15px;
}

.side-map .gm-style {
  border: 1px solid #ccc;
  border-width: 1px !important;
  border-radius: 4px;
}

.anchor {
  width: 100%;
}

.side-map.stick {
  position: fixed;
  top: 0px;
}

p.no-results {
  margin-top: 30px;
  font-size: 20px;
}

ul.pagination a {
  color: #3a89b7;
}

ul.pagination li.active a {
  background-color: #3a89b7;
  border-color: #3a89b7;
}

ul.pagination li.active a:focus {
  background-color: #3a89b7;
  border-color: #3a89b7;
}

.loading-overlay {
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -100px;
  margin-left: -100px;
  width: 200px;
  height: 200px;
  opacity: 1 !important;
}

@-webkit-keyframes uil-magnify-move {
  0% {
    -ms-transform: translate(30px, 30px);
    -moz-transform: translate(30px, 30px);
    -webkit-transform: translate(30px, 30px);
    -o-transform: translate(30px, 30px);
    transform: translate(30px, 30px);
  }
  33% {
