.uil-magnify-css {
  background: none;
  position: relative;
  width: 200px;
  height: 200px;
}

.uil-magnify-css .outer-circle {
  position: absolute;
  width: 110px;
  height: 110px;
  top: 30px;
  left: 30px;
  border: 15px solid #65a1c3;
  border-radius: 60px;
}

.uil-magnify-css .inner-circle {
  position: absolute;
  width: 80px;
  height: 80px;
  top: 45px;
  left: 45px;
  background: rgba(255, 255, 255, 0.25);
  border-radius: 30px;
}

.uil-magnify-css .grip {
  position: absolute;
  width: 24px;
  height: 60px;
  top: 100px;
  left: 118px;
  border-radius: 20px;
  background: #65a1c3;
  -ms-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.uil-magnify-css .group {
  -ms-animation: uil-magnify-move 1s linear infinite;
  -moz-animation: uil-magnify-move 1s linear infinite;
  -webkit-animation: uil-magnify-move 1s linear infinite;
  -o-animation: uil-magnify-move 1s linear infinite;
  animation: uil-magnify-move 1s linear infinite;
}

@media (max-width: 676px) {
  .business-result .business-pic {
    height: 60px;
    width: 60px;
    line-height: 53px;
  }
}
