div.ad-types {
  margin-bottom: 15px;
}

div.ad-types div.ad-type {
  border: 1px solid #ccc;
  padding-top: 20px;
  padding-bottom: 20px;
  border-radius: 4px;
  background: #f7f7f7;
}

div.ad-types h3 {
  margin-top: 0px;
}

div.ad-types p {
  font-size: 18px;
}

div.current-ads {
  min-height: 180px;
  margin-bottom: 25px;
}

div.current-ads p.no-ads {
  margin-top: 30px;
}

div.current-ads div.ad {
  border-bottom: 1px dotted #ccc;
}

div.current-ads div.ad:last-child {
  border-bottom: none;
}

form#create-ad .form-group {
  margin-top: 25px;
  clear: both;
}

form#create-ad .form-group:first-child {
  margin-top: 0px;
}

input.ad {
  height: 350px;
  position: relative;
  top: 0px;
}

div#ad-drop {
  height: 350px;
  border: 1px dashed #3a89b7;
}

div#ad-drop button.btn {
  transition: ease 300ms;
  color: white;
  background-color: #3a89b7;
  margin-top: -45px;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

div#ad-drop div.indicators {
  position: absolute;
  top: 0px;
  right: 0px;
}

div#ad-drop:hover {
  border: 1px dashed #6597bd;
}

div#ad-drop:hover button.btn {
  border-color: 1px dashed #6597bd;
  background-color: #6597bd;
}

.red {
  color: red;
}

.green {
  color: green;
}

div.due-today {
  padding-top: 10px;
  padding-bottom: 10px;
  border: 1px solid #ccc;
  background: #eee;
  border-radius: 4px;
  margin-bottom: 15px;
}

div.due-today h4 {
  font-size: 25px;
  font-weight: 500;
}

div.due-today a.btn {
  display: block;
  margin-top: 5px;
}

div.card {
  vertical-align: middle;
  min-height: 20px;
  border-bottom: 1px dotted #ddd;
  padding: 5px 0px 10px;
  margin-bottom: 5px;
}

div.card p {
  margin-top: 5px;
  margin-bottom: 0px;
}

div.cards-list {
  margin-bottom: 25px;
}
