#events-specials-form label {
  width: auto;
}

.events-specials-item {
  width: 100%;
  min-height: 50px;
  padding: 20px 0px 10px;
  border-bottom: 1px #ccc dotted;
}

.events-specials-item .icon {
  height: 100%;
  float: left;
  margin-right: 10px;
}

.events-specials-item .icon i {
  font-size: 28px;
}

.events-specials-item .info-box {
  float: left;
  height: 100%;
  max-width: 400px;
}

.events-specials-item .info-box h4 {
  margin-top: 0px;
}

.events-specials-item .info-box h4 small {
  text-transform: capitalize;
}

.events-specials-item .info-box .times {
  font-size: 13px;
}

.events-specials-item .info-box .description {
  font-size: 16px;
}

.events-specials-item:last-child {
  border-bottom: none;
}

@media (max-width: 767px) {
  .events-specials-item .info-box {
    max-width: 250px;
  }
}
