.left-business-section {
  border-right: 1px solid #ccc;
}

.left-business-section .form-group:first-child {
  margin-top: -25px;
}

.left-business-section input {
  margin-bottom: 10px;
}

.left-business-section div.radio {
  margin-top: 5px;
  padding-left: 0px;
}

.left-business-section div.radio label {
  margin-right: 20px;
  display: block;
  float: left;
  max-width: 50px;
  clear: none;
}

.right-business-section .form-group:first-child {
  margin-top: -25px;
}

.right-business-section .form-group {
  margin-top: 25px;
}

.big-checklist label {
  display: block;
  font-size: 13px;
}

input.logo {
  height: 350px;
  position: relative;
  top: 0px;
}

div#logo-drop {
  height: 350px;
  border: 1px dashed #3a89b7;
}

div#logo-drop button.btn {
  transition: ease 300ms;
  color: white;
  background-color: #3a89b7;
  margin-top: -45px;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

div#logo-drop:hover {
  border: 1px dashed #6597bd;
}

div#logo-drop:hover button.btn {
  border-color: 1px dashed #6597bd;
  background-color: #6597bd;
}

div#featured-drop {
  height: 250px;
}

div#featured-drop input.file-drop {
  height: 100%;
}

div#featured-drop div.logo-input-wrap {
  border: 1px dashed #3a89b7;
  clear: both;
  height: 150px;
}

div#featured-drop div.featured-thumb {
  min-height: 90px;
  background-position: center center;
  background-size: auto 100%;
  background-repeat: no-repeat;
  margin: 0px -7px 10px -15px;
}

div#featured-drop button.btn {
  transition: ease 300ms;
  color: white;
  background-color: #3a89b7;
  margin-top: -45px;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

div#featured-drop i.icon-camera {
  transition: ease 300ms;
  color: #3a89b7;
  font-size: 55px;
  display: block;
  text-align: center;
  margin: -121px auto 68px;
}

div#featured-drop span.close-me {
  transition: ease-in 300ms;
  opacity: 0.3;
  color: #3a89b7;
  margin-top: -8px;
  margin-right: -13px;
  font-size: 150%;
}

div#featured-drop span.close-me:hover {
  cursor: pointer;
  opacity: 1;
}

div#featured-drop:hover div.logo-input-wrap {
  border: 1px dashed #6597bd;
}

div#featured-drop:hover i.icon-camera {
  color: #6597bd;
}

div#featured-drop:hover button.btn {
  border-color: 1px dashed #6597bd;
  background-color: #6597bd;
}

div.logo-input-wrap {
  background-position: center 36%;
  background-size: contain;
  background-repeat: no-repeat;
}

div.sub-header {
  margin-top: 25px;
  padding-top: 20px;
  border-top: 1px solid #ccc;
}

label.day-label {
  font-size: 13px;
  white-space: nowrap;
  font-weight: 400;
  line-height: 40px;
  vertical-align: middle;
}

div.time-selector {
  padding-top: 4px;
}
