ul.rating-icons {
  list-style: none;
  padding-left: 0px;
  margin: 0px;
}

ul.rating-icons li {
  display: inline-block;
}

ul.rating-icons li img {
  max-width: 40px;
}

ul.rating-icons li img.inactive {
  filter: grayscale(100%);
  opacity: 0.5;
}

ul.rating-icons li img.active {
  filter: grayscale(0%);
  opacity: 0.85;
}

ul.rating-icons li img.hover {
  filter: grayscale(0%);
  opacity: 1;
}

ul.rating-icons li:hover {
  cursor: pointer;
}

ul.rating-icons.md li img {
  max-width: 34px;
}

ul.rating-icons.sm li img {
  max-width: 20px;
}
