section.big-bg {
  min-height: 600px;
  background: url(/assets/images/home-bg.jpeg) center center no-repeat;
  background-size: cover;
  color: white;
}

section.big-bg .row {
  padding-top: 130px;
}

section.big-bg h2 {
  font-size: 50px;
  margin-top: 5px;
  margin-bottom: 20px;
}

section.big-bg h4 {
  font-size: 35px;
  font-weight: 200;
}

section.big-bg p {
  font-size: 20px;
  font-weight: 200;
  margin-bottom: 15px;
}

section.alone {
  border-top: 1px solid #ccc;
  padding-top: 0px;
  padding-bottom: 50px;
}

section.diversity-types {
  background: #65a1c3;
  padding: 60px 0px;
}

section.diversity-types h2.header {
  font-size: 42px;
  color: white;
  margin: 10px 0px 60px;
  padding: 0px 100px;
  font-weight: 200;
}

section.diversity-types .thumbnail {
  background: none;
  border: none;
  overflow: hidden;
  padding: 0px;
  position: relative;
  max-height: 320px;
  margin-bottom: 20px;
}

section.diversity-types .thumbnail img {
  border-radius: 4px;
  margin-left: -10%;
  max-width: 120%;
  transition: 200ms ease-out;
}

section.diversity-types .thumbnail .vert-align {
  padding-top: 26%;
  height: 50%;
}

section.diversity-types .thumbnail .thumbnail-overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  transition: 200ms ease-out;
}

section.diversity-types .thumbnail .thumbnail-overlay h3 {
  font-size: 40px;
  letter-spacing: 1px;
  color: white;
  margin: 0px;
}

section.diversity-types .thumbnail .thumbnail-overlay p {
  font-size: 22px;
  color: #efefef;
  text-transform: uppercase;
}

section.diversity-types .thumbnail:hover .thumbnail-overlay {
  background: rgba(0, 0, 0, 0.75);
}

section.diversity-types .thumbnail:hover h3 {
  color: white;
}

section.diversity-types .thumbnail:hover img {
  margin-left: 0;
  max-width: 100%;
}

.icon-feature {
  padding: 30px 70px;
}

.icon-feature i {
  font-size: 60px;
}

.icon-feature p {
  font-size: 20px;
  font-weight: 300;
}

h1.header {
  font-size: 55px;
  font-weight: 300;
  margin: 60px 0px 50px;
}

section.join-form {
  margin-bottom: -20px;
}

section.join-form .side-image {
  background: url(/assets/images/side-img.jpeg) center center no-repeat;
  background-size: cover;
  min-height: 350px;
  height: 100%;
}

section.join-form .form {
  background: #EFEFEF;
  min-height: 350px;
  padding: 30px 40px;
  height: 100%;
}

section.join-form .form h3 {
  margin-bottom: 35px;
}

button.btn.btn-warning {
  background-color: #F7891F;
  border-color: #F7891F;
}

button.btn.btn-warning:hover {
  background-color: #f3a356;
  border-color: #f3a356;
}

button.btn.jumbo {
  padding: 10px 30px;
  min-width: 250px;
  font-size: 22px;
  font-weight: 300;
  letter-spacing: 1px;
}

button.shadow {
  box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.7);
}

@media (max-width: 767px) {
  section.big-bg .row {
    padding-top: 40px;
    text-align: center;
  }
  section.big-bg .row h2, section.big-bg .row h4, section.big-bg .row p {
    text-align: left;
  }
  section.big-bg button {
    margin-top: 40px;
  }
}
