header nav.navbar-default {
  max-height: 100px;
  border: none;
  border-radius: 0px;
  border-bottom: 1px solid #d0d0d0;
  padding-bottom: 5px;
  margin-bottom: 0px;
  background: white;
  background-size: cover;
  color: #333;
}

header nav.navbar-default li a {
  padding: 20px 10px;
}

header nav.navbar-default li.right-button {
  padding: 13px 0px 8px 5px;
}

header nav.navbar-default li.right-button a.btn-user {
  min-width: 150px;
}

header nav.navbar-default a.navbar-brand {
  margin-left: 0px !important;
  padding: 8px 15px 6px;
  height: 100%;
}

header nav.navbar-default img {
  max-height: 65px;
}

header nav.navbar-default div.container-fluid {
  width: 100%;
  padding-top: 5px;
}

header nav.navbar-default a.btn.btn-default {
  padding: 6px 12px;
}

header nav.navbar-default a.btn.btn-default.dropdown-toggle {
  background: #F7891F;
  color: white;
  border-color: #F7891F;
}

header nav.navbar-default .dropdown-menu p.dropdown-item {
  margin: 10px 0px;
}

header nav.navbar-default .dropdown-menu p.dropdown-item a {
  padding: 0px 10px;
}

.navbar-default .navbar-nav > li > a, .navbar-default .navbar-text {
  font-size: 14px;
}

@media (max-width: 767px) {
  header {
    z-index: 100;
  }
  header nav {
    z-index: 100;
  }
  header .navbar-form {
    margin-top: 0px;
  }
  header .navbar-collapse {
    background: #fcfbff;
    padding: 0px 35px;
    z-index: 101;
  }
  header .navbar-toggle {
    margin-top: 15px;
  }
  header .navbar-header {
    padding: 0px 10px;
  }
  header nav.navbar-default li a {
    font-size: 16px;
    padding: 11px 10px;
  }
  header nav.navbar-default img {
    max-height: 50px;
  }
  h2.section-header {
    z-index: 99;
    font-size: 20px;
  }
}
