#hot-spotz {
  min-height: 500px;
  margin-bottom: 20px;
}

.hot-tabs {
  border-top: 1px solid #ccc;
  border-left: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  border-right: 1px solid #fff;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  margin-right: -1px;
  z-index: 2;
}

.hot-tabs ul.nav {
  margin-left: -15px;
  margin-right: -15px;
  text-align: center;
}

.hot-tabs ul.nav li {
  background: #eee;
  border-right: 1px solid #ccc;
}

.hot-tabs ul.nav li.active {
  border-right: none;
  background: #fff;
}

.hot-tabs ul.nav li > a:focus {
  background: #fff;
}

.hot-tabs ul.nav li + li {
  margin-top: 0px;
  border-top: 1px solid #ccc;
}

.sm-search-list {
  border: 1px solid #ccc;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  min-height: 500px;
  padding: 5px 10px;
  max-height: 500px;
  overflow: auto;
}

.sm-search-list .business-result .business-pic {
  width: 60px;
  height: 50px;
  line-height: 25px;
}

.sm-search-list .business-result p {
  font-size: 13px;
}

.sm-search-list .loading-overlay {
  position: relative;
  width: 150px;
  height: 150px;
  top: 185px;
}

@media (max-width: 767px) {
  .hot-tabs {
    margin-right: 15px;
  }
  .hot-tabs ul.nav li.active {
    border-right: 1px solid #ccc;
    background: #fff;
  }
  .sm-search-list {
    height: auto;
    max-height: 5000px;
  }
}
