footer {
  margin-top: 20px;
  background: url(/assets/images/footer-bkg.png) #65a1c3 center center no-repeat;
  min-height: 200px;
  border-bottom: 15px solid transparent;
}

footer div.container-fluid {
  padding-top: 30px;
  padding-bottom: 30px;
  max-width: 1000px;
}

footer h3.sub-header {
  color: #fff;
}

footer ul.nav.nav-footer li a {
  color: #eee;
  padding: 6px 0px;
}

footer ul.nav.nav-footer li a i {
  margin-right: 5px;
  vertical-align: middle;
}

footer ul.nav.nav-footer li a:hover, footer ul.nav.nav-footer li a:focus {
  background: none;
  outline: none;
  color: white;
}

footer ul.nav.nav-footer li:first-child a {
  padding-top: 0px;
}

footer ul.nav.nav-footer li:last-child a {
  padding-bottom: 0px;
}

footer p {
  color: #eee;
  margin-top: 5px;
  font-size: 12px;
}

footer div.right-logo {
  padding-top: 30px;
}

@media (max-width: 767px) {
  footer {
    text-align: center;
  }
  footer .right-logo {
    text-align: center;
  }
}
